"use server";

import axios from "axios";

export const sendEmailAction = async (formData: FormData) => {
  try {
    const name = formData.get("name") as string;
    const email = formData.get("email") as string;
    const mesaj = formData.get("mesaj") as string;

    const data = {
      mailAdresleri: "saglam.mehmet@gmail.com",
      mesaj: mesaj,
      bolumler: [
        {
          alanlar: {
            Konu: "SMMMO İletişim Bölümünden Gönderildi",
            Mesaj: mesaj,
          },
          bolumAdi: "Kullanıcı Mesajı",
        },
        {
          alanlar: {
            "Adı Soyadı": name,
            "E-posta Adresi": email,
            Telefon: "",
          },
          bolumAdi: "İletişim Bilgileri",
        },
      ],
    };

    const url = `https://digipo.web.tr/api/mail/?tema=mail&fromMail=no-reply@postamerkezi.link&fromName=Eis%20YAYINLARI&data=${encodeURIComponent(
      JSON.stringify(data)
    )}`;

    const response = await axios.get(url, {
      headers: {
        "User-Agent":
          "Mozilla/5.0 (Windows; U; Windows NT 5.1; en-US; rv:1.8.1.13) Gecko/20080311 Firefox/2.0.0.13",
      },
    });

    if (response.status == 200) {
      /*Email başarıyla gönderildi*/
      return {
        sonuc: true,
        mesaj: "",
        response: null,
        hata: null,
      };
    } else {
      return {
        sonuc: false,
        mesaj: "Email gönderilemedi",
        response: response.data,
        hata: null,
      };
    }
  } catch (error) {
    return {
      sonuc: false,
      mesaj: "Hata catch",
      response: null,
      hata: error,
    };
  }
};
